import axios from 'axios'

const AXIOS = axios.create({
  baseURL: "http://localhost:8098/api",
  timeout: 10000
})

export default {
  response:[],
  hello () {
    return AXIOS.get(baseURL+"/hello")
  },
  getUser (userId) {
    return AXIOS.get(`/user/` + userId)
  },
  createUser (firstName, lastName) {
    return AXIOS.post(`/user/` + firstName + '/' + lastName)
  },
  getSecured (user, password) {
    return AXIOS.get(`/secured/`, {
      auth: {
        username: user,
        password: password
      }})
  },
  getTestQuery (query){
    return AXIOS.get(`/runquery/`+query)
  },
  getRunCachePrepare (){
    return AXIOS.get(`/runquery/cachePrepare`)
  },
  getReadAllMemcached (){
    axios({ method: "GET", "url": `/runquery/readAllMemcached` }).then(result => {
      this.response = result.data
      }, error => {
          console.error(error)
      })
    return this.response//AXIOS.get(`/runquery/readAllMemcached`)
  },
  getClearMemcached (){
    axios({ method: "GET", "url": `/runquery/readAllMemcached` }).then(result => {
      this.response = result.data
      }, error => {
          console.error(error)
      })
    return this.response//AXIOS.get(`/runquery/clearMemcached`)
  },
  getRunExperiment (){
    return AXIOS.get(`/runquery/experiment`)
  }
}



